import * as PIXI from 'pixi.js'
import { World, Body, Bodies, Vector } from 'matter-js'
import { GREATER_THAN_PLAYER_X_POS, GREATER_THAN_PLAYER_Y_POS, LESS_THAN_PLAYER_Y_POS } from '../constants/mouseStates'
import { PISTOL_BULLET_REMOVAL_TIMEOUT, PISTOL_SHELL_EJECTION_ANGULAR_VELOCITY_MIN, PISTOL_SHELL_EJECTION_ANGULAR_VELOCITY_MAX,
  WEAPON_TYPE_PISTOL, WEAPON_TYPE_SMG, WEAPON_TYPE_TURRET, WEAPON_TYPE_SHOTGUN
 } from '../constants/weapons'
import { playerCollisionOptions } from '../collisionOptions'
import { calcBulletAngle, getBodyWidth, getBodyHeight, getPlayerBody, getPlayerPartByType } from '../utils/Utils'
import { createGenericMessage, createTextMessage } from '../createMessages'
import { setLastShot, StatsDefs } from '../gameSlice'
import store from '../../../app/store'
import random from 'random'
import { BevelFilter } from '@pixi/filter-bevel'
import { MotionBlurFilter } from '@pixi/filter-motion-blur'
import { increaseStats } from '../gameSlice'
import { radians } from 'radians'

const { getState, dispatch } = store

export const calcPistolDamage = body => {
  let { min, max } = getState().game.player.weapons.damage[WEAPON_TYPE_PISTOL]
	if (body.label.indexOf('body') > -1) {
		return { part: 'torso', dmg: min }
	} else if (body.label.indexOf('head') > -1) {
		return { part: 'head', dmg: max }
	}
}

