// bullet impact
export function BulletImpact (surfaceType, position, scale) {
	let {x,y} = position
	let { gameObjects, TweenLite: { to, set, from } } = window
	let world = gameObjects.getKeyValue('world')
	let container = gameObjects.getKeyValue('game-pixi-container')
	let positionalBody = Bodies.rectangle(x,y,10,10,{isStatic:true,collisionFilter:playerCollisionOptions})
	World.add(world, positionalBody)

	let explosionAssetsSheet = gameObjects.getKeyValue('explosion-assets-sheet')
	let smokeAssets = [ 'smoke1.png', 'smoke2.png', 'smoke3.png', 'smoke4.png' ]
	const getRandomSmokeSprite = () => (
		new PIXI.Sprite(
			explosionAssetsSheet.textures[
				smokeAssets[ Math.floor( Math.random() * smokeAssets.length ) ]
			]
	))
	let randInt = 2 //random.int(2, 4)
	for (let i = 0; i < randInt; ++i) {
		let smokeSprite = getRandomSmokeSprite()
		if (surfaceType === 'enemy') {
			smokeSprite.tint = 0xFF0000
		}
		smokeSprite.pivot.x = smokeSprite.width * .5
		smokeSprite.pivot.y = smokeSprite.height * .5
		to(smokeSprite, scale && scale.alphaDuration ? scale.alphaDuration : .5, {
			alpha: 0,
			onComplete: () => {
				if (i === randInt - 1) {
					World.remove(world, positionalBody)
					world = null 
				}
			}
		})
		let randScale
		if (scale && scale.rand && scale.rand.min && scale.rand.max) {
			randScale = random.float(scale.rand.min, scale.rand.max)
		} else {
			randScale = random.float(.25, .5)
		}
		set(smokeSprite.scale, {
			x: scale && scale.set ? scale.set : .1,
			y: scale && scale.set ? scale.set : .1
		})
		to(smokeSprite.scale, scale && scale.duration ? scale.duration : .25, {
			x: randScale,
			y: randScale
		})
		let xyval = { x: 0, y: 0 }
		to(xyval, scale && scale.duration ? scale.duration : .25, {
			x: surfaceType === 'enemy' ? random.int(-20, 20) : random.int(-10, 10),
			y: surfaceType === 'enemy' ? random.int(-20, 20) : random.int(-50, 0)
		})
		to(smokeSprite.position, 3, {
			onUpdate: () => {
				if (this.calculatedPosition) {
					smokeSprite.position.x = this.calculatedPosition.x + xyval.x
					smokeSprite.position.y = this.calculatedPosition.y + xyval.y
					if (container && container.children.indexOf(smokeSprite) < 0) {
						container.addChild(smokeSprite)
					}
				}
			},
			onComplete: () => {
				smokeSprite.destroy()
				container = null
				gameObjects = null
			}
		})
	}

	this.renderPosition = (renderFn) => {
		this.calculatedPosition = renderFn(positionalBody)
	}

	return this
}

// bullet shell
export function PistolShell(x, y, gameObjects, changeLabelString, overrideOptions) {
	let world, container, shells
	let { game: { mouseState, player: { weapons: { equipped, shellEjectionForce, shellRemovalTimeout } } } } = getState()
	if (!container) { container = gameObjects.getKeyValue('game-pixi-container') }
	if (!world) { world = gameObjects.getKeyValue('world') }
	if (!shells) { shells = gameObjects.getKeyValue('pistol-shells') }
	// matter
	this.body = overrideOptions && overrideOptions.body
	? overrideOptions.body
	: Bodies.rectangle(
    mouseState.y === GREATER_THAN_PLAYER_Y_POS || mouseState.y === LESS_THAN_PLAYER_Y_POS
    ? x
    :
      mouseState.x === GREATER_THAN_PLAYER_X_POS
      ? x + 30
      : x - 30
    ,
    mouseState.y === LESS_THAN_PLAYER_Y_POS ? y - 40 : y - 20
    , 7, 4, {
		restitution: 0.75,
		collisionFilter: playerCollisionOptions
	})
	this.body.label = changeLabelString !== undefined ? `player ${changeLabelString} shell` : 'player pistol shell'
	if (equipped === WEAPON_TYPE_SHOTGUN) {
		this.body.label = 'shotgun shell'
	}
	World.add(world, this.body)
	Body.applyForce(this.body, this.body.position, overrideOptions && overrideOptions.force ? overrideOptions.force : {
		x: mouseState.x === GREATER_THAN_PLAYER_X_POS ? random.float(-shellEjectionForce[equipped].min, -shellEjectionForce[equipped].max) : random.float(shellEjectionForce[equipped].min, shellEjectionForce[equipped].max),
		y: random.float(-shellEjectionForce[equipped].min, -shellEjectionForce[equipped].max)
	})
	Body.setAngularVelocity(this.body, random.float(-PISTOL_SHELL_EJECTION_ANGULAR_VELOCITY_MIN, -PISTOL_SHELL_EJECTION_ANGULAR_VELOCITY_MAX))
	// pixi
	this.gfc = new PIXI.Graphics()
	this.gfc.beginFill(equipped === WEAPON_TYPE_SHOTGUN ? 0xB00000 : 0xAB931E)
	// if (!shellWd) {
	// }
	let shellWd = getBodyWidth(this.body)
	let shellHt = getBodyHeight(this.body)
	if (equipped === WEAPON_TYPE_SHOTGUN) {
		this.gfc.drawRect(0, 0, shellHt, shellWd + 1)
	} else {
		if (equipped === WEAPON_TYPE_SMG) {
			this.gfc.drawRect(0, 0, shellHt, shellWd + 2)
		} else {
			this.gfc.drawRect(0, 0, shellWd, shellHt)
		}
	}
	this.gfc.endFill()
	if (equipped === WEAPON_TYPE_SHOTGUN) {
		this.gfc.beginFill(0xCCB64D)
		this.gfc.drawRect(0, shellWd + 1, shellHt, 3)
		// this.gfc.rotation = radians(45)
	}
	if (!window.gameObjects.getKeyValue('bullet-shell-bevel')) {
		window.gameObjects.setKeyValue('bullet-shell-bevel', new BevelFilter({ lightAlpha: 0.5, shadowAlpha: 0.5 }))
	}
	let filters = [ window.gameObjects.getKeyValue('bullet-shell-bevel') ]
	this.gfc.filters = getState().game.filterEffects ? filters : []
	this.gfc.pivot.x = this.gfc.width/2
	this.gfc.pivot.y = this.gfc.height/2
	container.addChild(this.gfc)
	shells.push(this)
	// methods
	this.destroy = function () {
		container.removeChild(this.gfc)
		World.remove(world, this.body)
		this.body = null
		this.gfc = null
		shells.splice(shells.indexOf(this), 1)
		world = null
		container = null
		gameObjects = null
		shells = null
	}
	// auto removal
	setTimeout(this.destroy.bind(this), shellRemovalTimeout[equipped])
	return this
}

export function PistolBullet(x, y, gameObjects, changeLabelString, overrideOptions, extraDamageBool) {
	let world, container
	let arm, bullets, bulletWd
	arm = getPlayerPartByType('arm', gameObjects) 
  let { game: { mouseRelativeToPlayerPosition, mouseState } } = getState()
	if (!container) { container = gameObjects.getKeyValue('game-pixi-container') }
	if (!world) { world = gameObjects.getKeyValue('world') }
	if (!bullets) { bullets = gameObjects.getKeyValue('pistol-bullets') }
  this.body = overrideOptions && overrideOptions.overrideBody ? overrideOptions.overrideBody : Bodies.circle(
    mouseState.y === GREATER_THAN_PLAYER_Y_POS || mouseState.y === LESS_THAN_PLAYER_Y_POS
    ? x
    :
      mouseState.x === GREATER_THAN_PLAYER_X_POS
      ? x + 50
      : x - 50
    ,
    y, 4, {
		restitution: 0.3
  })

	this.body.label = (
		extraDamageBool ? 'extra damage bullet'
		:
			changeLabelString !== undefined && changeLabelString !== null ?
			`player ${changeLabelString} bullet`
			:  'player pistol bullet'
	)
	this.body.main = this
  World.add(world, this.body)
	Body.setAngle(this.body, overrideOptions && overrideOptions.overrideAngle ? overrideOptions.overrideAngle : Vector.angle(this.body.position, mouseRelativeToPlayerPosition))
  Body.applyForce(this.body, this.body.position, overrideOptions && overrideOptions.overrideForce ? overrideOptions.overrideForce : calcBulletAngle(arm.position, mouseRelativeToPlayerPosition))

	// pixi 
	this.gfc = new PIXI.Graphics()
	this.gfc.beginFill(0xFFFFFF)
	if (!bulletWd) { bulletWd = getBodyWidth(this.body) }
	this.gfc.drawRect(0, 0, bulletWd, (bulletWd/3))
	this.gfc.endFill()
	this.gfc.pivot.x = this.gfc.width/2
	this.gfc.pivot.y = this.gfc.height/2
	container.addChild(this.gfc)
	bullets.push(this)
	// // add bullet trail
  // let tier = store.getState().game.player.modTiers['powder'][equipped].current
	// this.bulletTrail = new PIXI.Graphics()
	// this.bulletTrail.lineStyle(
	// 	2,
	// 	(
	// 		overrideOptions ? 0xFFFFFF
	// 		:
	// 			tier === 1 ? 0xFFFFFF
	// 			:
	// 				tier === 2 ? 0xFFD100
	// 				:
	// 					tier === 3 ? 0xFF6D00
	// 					:
	// 						tier === 4 ? 0xFF0000
	// 						: 0xFFFFFF
	// 	),
	// 	0.8
	// )
	// this.linePoints = []
	// this.lineDrawn = false
	// this.drawLine = () => {
	// 	if (this.lineDrawn === false && getState().game.player.weapons.equipped !== WEAPON_TYPE_SHOTGUN) {
	// 		setTimeout(() => {
	// 			if (this.linePoints) {
	// 				this.linePoints.forEach((p, i) => {
	// 					if (this.linePoints[i+1]) {
	// 						this.bulletTrail.moveTo(p.x, p.y)
	// 						this.bulletTrail.lineTo(this.linePoints[i+1].x, this.linePoints[i+1].y)
	// 					}
	// 				})
	// 			}
	// 		}, 200)
	// 		this.lineDrawn = true
	// 	}
	// }
	// container.addChild(this.bulletTrail)

	// methods
	this.addImpactEffect = (surfaceType, pos) => {
		let bulletImpacts = gameObjects.getKeyValue('bullet-impacts')
		let impact = new BulletImpact(surfaceType, pos)
		bulletImpacts.push(impact)
	}

	this.destroyed = false
	this.destroy = function (surfaceType) {
		if (!this.destroyed) {
			let bulletLastPos = this.body.position
			this.destroyed = true
			container.removeChild(this.gfc)
			container.removeChild(this.bulletTrail)
			World.remove(world, this.body)
			this.linePoints = null
			this.body = null
			this.gfc = null
			if (bullets.indexOf(this) > -1) {
				bullets.splice(bullets.indexOf(this), 1)
			}
			if (surfaceType) {
				// add bullet collision impact effect depending on the surface hit
				this.addImpactEffect(surfaceType, bulletLastPos)
			}
			world = null
			container = null
			gameObjects = null 
		}
	}

	// auto removal
	setTimeout(this.destroy.bind(this), PISTOL_BULLET_REMOVAL_TIMEOUT)
	return this
}

const fireEquippedWeapon = (gameObjects) => {
	let world, weaponsUI, player, playerBody, playerBodyWd, playerBodyHt, arm
	if (!world) { world = gameObjects.getKeyValue('world') }
  if (!weaponsUI) { weaponsUI = gameObjects.getKeyValue('weapons-ui') }
  if (!player) { player = gameObjects.getKeyValue('player') }
	let { game: { player: { weapons: { lastShot, totalAmmo, equipped, shotLimiter } } } } = getState()
	if (JSON.parse(lastShot[equipped]) + shotLimiter[equipped] < Date.now() && !weaponsUI.checkEmpty(equipped) && !weaponsUI.reloading && totalAmmo[equipped] > 0) {
		dispatch( setLastShot(equipped, JSON.stringify(Date.now())) )
		dispatch( increaseStats( StatsDefs.bulletsFired ) )
    weaponsUI.fireBullet(equipped)
    player.fireWeaponAnimation()
		if (!playerBody) { playerBody = getPlayerBody(gameObjects) }
		if (!playerBodyWd) { playerBodyWd = getBodyWidth(playerBody) }
		if (!playerBodyHt) { playerBodyHt = getBodyHeight(playerBody) }
		if (!arm) { arm = getPlayerPartByType('arm', gameObjects) }
		let extraDmg = (
			weaponsUI.lastBulletByWepType[equipped] && getState().game.player.perks['lastBullet'].owned ? true : false
		)
		if (equipped === WEAPON_TYPE_SHOTGUN) {
			new PistolShell(0, 0, gameObjects, null, {
				body: Bodies.rectangle(playerBody.position.x, arm.position.y, 10, 4, { collisionFilter: playerCollisionOptions })
			})
		} else {
			new PistolShell(playerBody.position.x, arm.position.y, gameObjects, (
				equipped !== WEAPON_TYPE_PISTOL ? equipped.split(' ')[0] : undefined
			))
		}
		if (equipped === WEAPON_TYPE_SHOTGUN) {
			for (let i = 0; i < 10; ++i) {
				new PistolBullet(arm.position.x, arm.position.y, gameObjects, 'shotgun', extraDmg)
			}
		} else {
			new PistolBullet(
				arm.position.x,
				arm.position.y,
				gameObjects,
				(equipped !== WEAPON_TYPE_PISTOL ? equipped.split(' ')[0] : undefined),
				null,
				extraDmg
			)
		}

		world = null 
		weaponsUI = null
		player = null
		playerBody = null
		playerBodyWd = null
		playerBodyHt = null
		arm = null 

	} else if (totalAmmo[equipped] === 0 && getState().game.showingMessage === false) {
		// createTextMessage('Out of ammo!', '#FF0000', .25, .75)
		// make click noise or some other indicator
		if (!weaponsUI.shaking) {
			weaponsUI.changeTextForEmptyShake('EMPTY')
			weaponsUI.emptyMagShake()
		}
	} else if (weaponsUI.checkEmpty(equipped) && getState().game.showingMessage === false) {
		if (!weaponsUI.reloading) {
			// createGenericMessage('Reload your weapon!', '#FF0000', .25, .75)
			// createTextMessage('Reload your weapon!', '#FF0000', .25, .75)
			// make empty mag sound or shake weapon UI
			if (!weaponsUI.shaking) {
				weaponsUI.changeTextForEmptyShake('RELOAD')
				weaponsUI.emptyMagShake()
			}
    }
	}
}

export const firePistol = (gameObjects) => {
	fireEquippedWeapon(gameObjects)
}

export const fireSMG = (gameObjects) => {
	fireEquippedWeapon(gameObjects)
}

export const fireShotgun = (gameObjects) => {
	fireEquippedWeapon(gameObjects)
}
