import { Bodies, Body, World } from 'matter-js'
import { Sprite } from 'pixi.js'
import random from 'random'

let { TweenLite: { delayedCall, to } } = window

export default function Gib ({ position_x, position_y }, { force_x, force_y }, timeToDisappear) {
  let { gameObjects } = window
  let gibs = gameObjects.getKeyValue('gibs')
  let world = gameObjects.getKeyValue('world')
  let container = gameObjects.getKeyValue('game-pixi-container')
  let body = Bodies.circle(position_x, position_y, 5, {
    collisionFilter: {
      category: 2,
      mask: 1
    }
  })
  body.label = 'gib'
  World.add(world, body)

  let getTexturePNG = () => {
    let gibs = [ 'gib.png', 'gib2.png', 'gib3.png', 'gib4.png' ]
    return gibs[ Math.floor( Math.random() * gibs.length ) ]
  }
  
  let partsSheet = gameObjects.getKeyValue('player-parts-sheet')
  let gfc = new Sprite(
    partsSheet.textures[ getTexturePNG() ]
  )
  container.addChild( gfc )
  
  gibs.push( this )

  gfc.pivot.x = gfc.width * .5
  gfc.pivot.y = gfc.height * .475

  Body.applyForce(body, { x: position_x, y: position_y }, { x: random.float(force_x * .25, force_x), y: random.float(force_y * .25, force_y) })
  
  this.animate = renderFn => {
    let { x, y, rotation: rot } = renderFn(body)
    gfc.position = { x, y }
    gfc.rotation = rot
  }

  this.destroy = () => {
    if (gfc) {
      gfc.destroy()
    }
    if (body) {
      World.remove(world, body)
    }
    let idx = gibs.indexOf(this)
    if ( idx > -1 ) {
      gibs.splice( idx, 1 )
    }
    world = null
    body = null
    container = null
    gibs = null
    gameObjects = null
  }

  delayedCall(timeToDisappear, () => {
    to(gfc, 2, { alpha: 0, onComplete: () => { this.destroy() }})
  })

  return this

}

export function GibGenerator (count, position, force, timeToDisappear) {
  let { x: position_x, y: position_y } = position
  force = force || { force_x: 0, force_y: 0 }
  let { x: force_x, y: force_y } = force
  // add some gibs for effect!
	for (let gibCnt = 0; gibCnt < count; ++gibCnt) {
		new Gib({ position_x, position_y }, { force_x, force_y }, timeToDisappear)
	}
}
