import { Texture } from '@pixi/core'
import { TilingSprite } from '@pixi/sprite-tiling'
import store from '../../app/store'

export default class WallTile extends TilingSprite {
    constructor() {
        // console.log(gameObjects)
        const texture = Texture.from(
          window.gameObjects.getKeyValue('wall-tile')
        )
        super(texture, 1, texture.height) //width 1 because we will call onResize from App anyway
        this.width = window.gameObjects.getKeyValue('game-width')
        this.height = window.gameObjects.getKeyValue('game-height')
        this.alpha = .2
    }

    onResize(width, height) {
        // this.width = width
    }

    onUpdate(delta) {
    	this.tilePosition.x -= delta * 4
    }

    renderPosition(renderFn, body, acceleration) {
      let { x, y } = renderFn(body)
      let { getState } = store
      let { game: { keys, player: { variables: { currentDirection, jumping } } } } = getState()
      if (currentDirection === 'right' && keys.indexOf(68) > -1) {
        this.tilePosition.x -= .35 //(acceleration * .25)
      }
      if (currentDirection === 'left' && keys.indexOf(65) > -1) {
        this.tilePosition.x += .35 //(acceleration * .25)
      }
      if (jumping) {
        this.tilePosition.y += .25
      }
      // let player = window.gameObjects.getKeyValue('player')
      // console.log( player.body )
    }
}