  this.grenadeSpritesByEquipped = {
    'pistol weapon': grenadeThrowPistolAnim,
    'smg weapon': grenadeThrowUMPAnim,
    'shotgun weapon': grenadeThrowShotgunAnim
  }
  
  this.ejectedMags = []

  this.reloadMagazineActionsByType = {
    'pistol weapon': () => {
      let { game: { player: { weapons: { equipped, reloadSpeed }, perks: { magDmg: { owned: magDmgOwned } } }, mouseState: { x: mx } } } = getState()
      pistolReloadSound.play()
      pistolReloadSound.fade(1, 0, 300)
      // make a box body
      let _d = mx === GREATER_THAN_PLAYER_X_POS ? 'left' : 'right'
      let magBody = Bodies.rectangle(
        this.body.position.x + (_d === 'left' ? 20 : -20),
        this.body.position.y, 7, 20, {
          collisionFilter: magDmgOwned ? {} : itemCollisionOptions
        })
      magBody.label = 'magazine'
      World.add(world, magBody)
      Body.applyForce(
        magBody,
        this.body.position,
        {
          x:  + (_d === 'left' ? random.float(0.0025, 0.005) : random.float(-0.0025, -0.005)),
          y: random.float(-0.0025, -0.005)
        })
      // make some simple magazing gfx
      let pistolMag = new PIXI.Graphics()
      pistolMag.beginFill(0x333333)
      pistolMag.drawRect(0, 0, 7, 20)
      pistolMag.pivot.x = pistolMag.width/2
      pistolMag.pivot.y = pistolMag.height/2
      let magObj = {
        gfx: pistolMag,
        body: magBody
      }
      this.ejectedMags.unshift(magObj)
      container.addChild(pistolMag)
      // cleanup
      delayedCall(2, () => {
        World.remove(world, magBody)
        pistolMag.destroy()
        this.ejectedMags.pop()
      })
    },
    'smg weapon': () => {
      let { game: { player: { perks: { magDmg: { owned: magDmgOwned } } }, mouseState: { x: mx } } } = getState()
      pistolReloadSound.play()
      pistolReloadSound.fade(1, 0, 300)
      // make a box body
      let _d = mx === GREATER_THAN_PLAYER_X_POS ? 'left' : 'right'
      let magBody = Bodies.rectangle(
        this.body.position.x + (_d === 'left' ? 20 : -20),
        this.body.position.y - (getBodyHeight(this.body)/2), 7, 42, {
          collisionFilter: magDmgOwned ? {} : itemCollisionOptions
      })
      magBody.label = 'magazine'
      World.add(world, magBody)
      Body.applyForce(
        magBody,
        this.body.position,
        {
          x:  + (_d === 'left' ? random.float(0.004, 0.01) : random.float(-0.004, -0.01)),
          y: random.float(-0.0015, -0.01)
        })
      // make some simple magazing gfx
      let smgMag = new PIXI.Graphics()
      smgMag.beginFill(0x333333)
      smgMag.drawRect(0, 0, 7, 42)
      smgMag.pivot.x = smgMag.width/2
      smgMag.pivot.y = smgMag.height/2
      let magObj = {
        gfx: smgMag,
        body: magBody
      }
      this.ejectedMags.unshift(magObj)
      container.addChild(smgMag)
      // cleanup
      delayedCall(2, () => {
        World.remove(world, magBody)
        smgMag.destroy()
        this.ejectedMags.pop()
      })
    },
    'shotgun weapon': () => {
      const reloadShell = () => {
        delayedCall(.3, () => {
          if (this.shotgunShellLoadIterations > 5) {
            charReloadShotgunSprite.gotoAndPlay(21)
          } else {
            charReloadShotgunSprite.gotoAndPlay(10)
            this.shotgunShellLoadIterations++
            reloadShell()
          }
        })
      }
      reloadShell()
    }
  }

