import { Bodies, World } from 'matter-js'
import { Graphics, Container, filters} from 'pixi.js'
import { itemCollisionOptions } from '../collisionOptions'
import { BevelFilter } from '@pixi/filter-bevel'
import store from '../../../app/store'

export default function CreditDrop (position, amount) {

  let { gameObjects } = window
  let world = gameObjects.getKeyValue('world')
  let container = gameObjects.getKeyValue('game-pixi-container')
  let currencyDrops = gameObjects.getKeyValue('currency-drops')
  let { x: _x, y: _y } = position

  // init
  // matter body 
  let creditBody = Bodies.circle(_x, _y, amount * .25, { restitution: .5, collisionFilter: itemCollisionOptions })
  creditBody.label = 'currency drop'
  creditBody.amount = amount
  // console.log(creditBody.amount)
  World.add(world, creditBody)
  // gfx -- blurred
  let creditContainer = new Container()
  let creditBlur = new Graphics()
  creditBlur.beginFill(0x0eead8)
  creditBlur.drawCircle(0, 0, amount * .25)
  let blurFilter = new filters.BlurFilter()
  blurFilter.blur = 3
  creditBlur.filters = store.getState().game.filterEffects ? [ blurFilter ] : []
  creditBlur.pivot.x = (creditBlur.width * .25) * .5
  creditBlur.pivot.y = (creditBlur.height * .25) * .5
  creditBlur.scale.x = 1.1
  creditBlur.scale.y = 1.1
  creditContainer.addChild(creditBlur)
  // gfx -- clear
  let creditGfc = new Graphics()
  creditGfc.beginFill(0x0eead8)
  creditGfc.drawCircle(0, 0, amount * .25)
  creditGfc.pivot.x = (creditGfc.width * .25) * .5
  creditGfc.pivot.y = (creditGfc.height * .25) * .5
  creditContainer.addChild(creditGfc)
  let bevel = new BevelFilter()
  bevel.lightColor = 0xffffff
  bevel.lightAlpha = .3
  bevel.shadowColor = 0x0
  bevel.shadowAlpha = .3
  creditContainer.filters = store.getState().game.filterEffects ? [ bevel ] : []
  // add to stage
  container.addChild(creditContainer)
  
  currencyDrops.push(this)

  creditBody.main = this

  this.creditBody = creditBody

  this.checkFilters = () => {
    if (store.getState().game.filterEffects) {
      creditContainer.filters = [bevel]
      creditBlur.filters = [blurFilter]
    } else {
      creditContainer.filters = []
      creditBlur.filters = []
    }
  }

  this.destroyed = false
  this.destroy = () => {
    if (!this.destroyed) {
      this.destroyed = true
      let idx = currencyDrops.indexOf(this)
      if (idx > -1) {
        currencyDrops.splice(idx, 1)
        creditContainer.destroy()
        World.remove(world, creditBody)
        world = null
        container = null
        currencyDrops = null
        gameObjects = null
        creditBody = null
        this.creditBody = null
        creditContainer = null
      }
    }
  }

  setTimeout(() => { this.destroy() }, 20000)

  this.positionCreditGfx = (renderFn) => {
    let { x, y, rotation: rot } = renderFn(creditBody)
    creditContainer.position = { x, y }
    creditContainer.rotation = rot
  }

  return this

} 