import { World, Bodies, Body } from 'matter-js'
import * as PIXI from 'pixi.js'
import random from 'random'
import Rock from '../items/Rock'
import { playerCollisionOptions } from '../collisionOptions'
import WoodPlank from '../items/WoodPlank'
import ExplosionSprite from './ExplosionSprite'
import { Howl, Howler } from 'howler'

export default function GrenadeExplosion (position, showRocksBool, options) {
  let { gameObjects, TweenLite: { to } } = window
  let explosion1 = window.gameObjects.getKeyValue('explosion1')
  let explosionSound1 = new Howl({
    src: [ explosion1 ]
  })
  let distantExplosion = window.gameObjects.getKeyValue('distant-explosion')
  let distantExplosionSound = new Howl({
    src: [ distantExplosion ]
  })
  let world = gameObjects.getKeyValue('world')
  let rocks = gameObjects.getKeyValue('rocks')
  let container = gameObjects.getKeyValue('game-pixi-container')
  let explosionsArray = gameObjects.getKeyValue('explosions')
  let explosionAssetsSheet = gameObjects.getKeyValue('explosion-assets-sheet')
  let smokeAssets = [ 'smoke1.png', 'smoke2.png', 'smoke3.png', 'smoke4.png' ]

  const getRandomSmokeSprite = () => (
    new PIXI.Sprite(
      explosionAssetsSheet.textures[
        smokeAssets[ Math.floor( Math.random() * smokeAssets.length ) ]
      ]
  ))
  let positionalBody = Bodies.rectangle(position.x, position.y, 20, 20, {
    restitution: 0,
    collisionFilter: playerCollisionOptions,
    isStatic: true
  })
  World.add(world, positionalBody)
  this.calcRenderedPosition = renderFn => {
    let { x, y } = renderFn(positionalBody)
    this.calculatedPosition = { x, y }
    // if (container && container.children.indexOf(smokeSprite) === -1) {
    //   container.addChild(smokeSprite)
    // }
  }
  explosionsArray.push( this )
  // put it together
  // smoke animation 
  let randInt = random.int(5, 10) // reduced from (10, 20)
  for (let s = 0; s < randInt; ++s) {
    let smokeSprite = getRandomSmokeSprite()
    smokeSprite.pivot.x = smokeSprite.width * .5
    smokeSprite.pivot.y = smokeSprite.height * .5
    let duration = 2 // smoke tween duration 
    to(smokeSprite, duration, {
      onStart: () => { container.addChild(smokeSprite) },
      alpha: 0,
      onComplete: () => {
        if (s === randInt - 1) {
          World.remove(world, positionalBody)
          let idx1 = explosionsArray.indexOf(this)
          if (idx1 > -1) { explosionsArray.splice(idx1, 1) }
          world = null
          explosionsArray = null
          explosionAssetsSheet = null
          smokeAssets = null
        }
      }
    })
    let randScale = random.int(1, 2)
    to(smokeSprite.scale, .5, { x: randScale, y: randScale })
    let xyval = { x: 0, y: 0 }
    to(xyval, .5, { x: random.int(-100, 100), y: random.int(-50, 50) })
    to(smokeSprite.position, duration, {
      onUpdate: () => {
        if (this.calculatedPosition) {
          smokeSprite.position.x = this.calculatedPosition.x + xyval.x
          smokeSprite.position.y = this.calculatedPosition.y + xyval.y
        }
      },
      onComplete: () => {
        smokeSprite.destroy()
        container = null
        gameObjects = null
      }
    })
  }
  let woodPlanks = gameObjects.getKeyValue('wood-planks')
  // rocks 
  if (position.y > 300 || showRocksBool) {
    for (let r = 0; r < random.int(4, 7); ++r) {
      if (options && options.showWood) {
        woodPlanks.push(new WoodPlank(position))
      } else {
        rocks.push(new Rock(position))
      }
    }
    woodPlanks = null
    rocks = null
  }
  if (!options || options.showExplosionSprite) {
    // blender explosion sprite
    let explosionSprites = gameObjects.getKeyValue('explosion-sprites')
    explosionSound1.seek(.5)
    explosionSound1.play()
    distantExplosionSound.play()
    explosionSprites.push(
      new ExplosionSprite(random.int(1, 2), positionalBody)
    )
    explosionSprites = null
  }
}