// anything that we can't manage with redux will go here,
// meaning full objects and functions with methods for
// manipulating Matter objects
export default function createGameObjects() {
	let state = new State()
	function State() {
		this.state = {}
		this.getKeyValue = key => {
      if (this.state[key]) {
        return this.state[key]
      } else {
        return false
      }
    }
		this.setKeyValue = (key, value) => {
      this.state[key] = value
      return this.state[key]
    }
		this.getFullState = () => { return this.state }
		this.setFullState = (obj) => { this.state = obj }
	}
	return state
}
