import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import MenuComponents from './MenuComponents'
import { Inline } from './ShopMenu'
import { gameStateTransition, setSelectedBarrierToBuild, setSelectedTrapToBuild } from '../gameSlice'
import GameStates, { SubStates } from '../constants/GameStates'
import { barrierTypes, trapTypes } from '../constants/buildTypes'
import { CustomMenuButton, BackButton } from './ShopMenu'

export default () => {

  const { gameObjects } = window
  const dispatch = useDispatch()
  const width = gameObjects.getKeyValue('game-width')
  const height = gameObjects.getKeyValue('game-height')
  const screenUI = gameObjects.getKeyValue('screen-ui')
  const gameState = useSelector(state => state.game.gameState)
  const gameSubState = useSelector(state => state.game.gameSubState)
  const BuildMenuStates = {
    MAIN: 'main',
    BARRIERS: 'barriers',
    TRAPS: 'traps'
  }
  const [buildMenuState, setBuildMenuState] = React.useState(BuildMenuStates.MAIN)
  const barrierCosts = useSelector(state => state.game.barriers.costToBuild)
  const trapCosts = useSelector(state => state.game.traps.costToBuild)
  const credits = useSelector(state => state.game.player.credits)

  const backToShopQuery = () => {
    screenUI.unpause()
    dispatch(
      gameStateTransition('main', gameState, GameStates.GAMEPLAY)
    )
    dispatch(
      gameStateTransition('sub', gameSubState, SubStates.SHOP_QUERY_SUBMENU)
    )
  }

  const showBarriersSubMenu = () => {
    setBuildMenuState(
      BuildMenuStates.BARRIERS
    )
  }

  const showTrapsSubMenu = () => {
    setBuildMenuState(
      BuildMenuStates.TRAPS
    )
  }

  const backToBuildMenu = () => {
    setBuildMenuState(
      BuildMenuStates.MAIN
    )
  }

  const gotoPlacementMode = () => {
    let screenUI = gameObjects.getKeyValue('screen-ui')
    screenUI.unpause()
    dispatch(
      gameStateTransition('main', gameState, GameStates.GAMEPLAY)
    )
    dispatch(
      gameStateTransition('sub', gameSubState, SubStates.BUILD_PLACEMENT_MODE)
    )
  }

  const replaceCursorWithObjectToPlace = () => {
    let crosshair = gameObjects.getKeyValue('crosshair')
    crosshair.buildArrow(0xFFFFFF)
  }
  
  const chooseBarrier = filteredType => {
    if (credits >= barrierCosts[filteredType]) {
      dispatch( setSelectedBarrierToBuild(filteredType) )
      dispatch( setSelectedTrapToBuild(null) )
      replaceCursorWithObjectToPlace()
      setTimeout(() => { gotoPlacementMode() }, 50)
    }
  }
  
  const chooseTrap = filteredType => {
    if (credits >= trapCosts[filteredType]) {
      dispatch( setSelectedTrapToBuild(filteredType) )
      dispatch( setSelectedBarrierToBuild(null) )
      replaceCursorWithObjectToPlace()
      setTimeout(() => { gotoPlacementMode() }, 50)
    }
  }

  return (
    <>
      <MenuComponents.Container width={width * .75} height={height * .75}>
        <MenuComponents.Backing width={width * .75} height={height * .75}></MenuComponents.Backing>
        {
          buildMenuState === BuildMenuStates.TRAPS ?
          <>
            <MenuComponents.MenuTitle>Build Traps</MenuComponents.MenuTitle>
            <MenuComponents.MenuButtonContainer>
              <MenuComponents.MenuButtonSpacer>
              <MenuComponents.MenuButtonSpacer>
                {
                  Object.keys(trapTypes).filter((trap, i) => i % 2 == 0 ).map((type, i) => (
                    <Inline key={i}>
                      <MenuComponents.MenuButtonSpacer>
                        <CustomMenuButton
                        width={ trapTypes[type].length > 14 ? 300 : 200 }
                        onClick={ () => chooseTrap( trapTypes[type] ) }>
                          { trapTypes[type] } <span style={{
                            color: credits >= trapCosts[trapTypes[type]] ? '#ffffff' : '#ff0000'
                          }}>${ trapCosts[trapTypes[type]] }</span>
                        </CustomMenuButton>
                      </MenuComponents.MenuButtonSpacer>
                    </Inline>
                  ))
                }
                </MenuComponents.MenuButtonSpacer>
                <MenuComponents.MenuButtonSpacer>
                {
                  Object.keys(trapTypes).filter((trap, i) => i % 2 == 1 ).map((type, i) => (
                    <Inline key={i}>
                      <MenuComponents.MenuButtonSpacer>
                        <CustomMenuButton
                        width={ trapTypes[type].length > 14 ? 300 : 200 }
                        onClick={ () => chooseTrap( trapTypes[type] ) }>
                          { trapTypes[type] } <span style={{
                            color: credits >= trapCosts[trapTypes[type]] ? '#ffffff' : '#ff0000'
                          }}>${ trapCosts[trapTypes[type]] }</span>
                        </CustomMenuButton>
                      </MenuComponents.MenuButtonSpacer>
                    </Inline>
                  ))
                }
                </MenuComponents.MenuButtonSpacer>
                <MenuComponents.MenuButtonSpacer>
                  <BackButton
                  onClick={ backToBuildMenu }>
                    Back
                  </BackButton>
                </MenuComponents.MenuButtonSpacer>

              </MenuComponents.MenuButtonSpacer>
            </MenuComponents.MenuButtonContainer>
          </>
          : null
        }
        {
          buildMenuState === BuildMenuStates.BARRIERS ?
          <>
            <MenuComponents.MenuTitle>Build Barriers</MenuComponents.MenuTitle>
            <MenuComponents.MenuButtonContainer>
              <MenuComponents.MenuButtonSpacer>
                {
                  Object.keys(barrierTypes).filter((trap, i) => i % 2 == 1 ).map((type, i) => (
                    <Inline key={i}>
                      <MenuComponents.MenuButtonSpacer>
                        <CustomMenuButton
                        width={ barrierTypes[type].length > 14 ? 300 : 200 }
                        onClick={ () => chooseBarrier( barrierTypes[type] ) }>
                          { barrierTypes[type] } <span style={{
                            color: credits >= barrierCosts[barrierTypes[type]] ? '#ffffff' : '#ff0000'
                          }}>${ barrierCosts[barrierTypes[type]] }</span>
                        </CustomMenuButton>
                      </MenuComponents.MenuButtonSpacer>
                    </Inline>
                  ))
                }
                </MenuComponents.MenuButtonSpacer>
                <MenuComponents.MenuButtonSpacer>
                {
                  Object.keys(barrierTypes).filter((trap, i) => i % 2 == 0 ).map((type, i) => (
                    <Inline key={i}>
                      <MenuComponents.MenuButtonSpacer>
                        <CustomMenuButton
                        width={ barrierTypes[type].length > 14 ? 300 : 200 }
                        onClick={ () => chooseBarrier( barrierTypes[type] ) }>
                          { barrierTypes[type] } <span style={{
                            color: credits >= barrierCosts[barrierTypes[type]] ? '#ffffff' : '#ff0000'
                          }}>${ barrierCosts[barrierTypes[type]] }</span>
                        </CustomMenuButton>
                      </MenuComponents.MenuButtonSpacer>
                    </Inline>
                  ))
                }
                <MenuComponents.MenuButtonSpacer>
                <MenuComponents.MenuButtonSpacer padding={20}></MenuComponents.MenuButtonSpacer>
                  <BackButton
                  onClick={ backToBuildMenu }>
                    Back
                  </BackButton>
                </MenuComponents.MenuButtonSpacer>

              </MenuComponents.MenuButtonSpacer>
            </MenuComponents.MenuButtonContainer>
          </>
          : null
        }
        {
          buildMenuState === BuildMenuStates.MAIN ?
            <>
              <MenuComponents.MenuTitle>Build Menu</MenuComponents.MenuTitle>
              <MenuComponents.MenuButtonContainer>
                <MenuComponents.MenuButtonSpacer>
                  <Inline>
                    <MenuComponents.MenuButtonSpacer>
                      <CustomMenuButton
                      onClick={ showBarriersSubMenu }>
                        Barriers
                      </CustomMenuButton>
                    </MenuComponents.MenuButtonSpacer>
                  </Inline>
                  <Inline>
                    <MenuComponents.MenuButtonSpacer>
                      <CustomMenuButton
                      onClick={ showTrapsSubMenu }>
                        Traps
                      </CustomMenuButton>
                    </MenuComponents.MenuButtonSpacer>
                  </Inline>
                </MenuComponents.MenuButtonSpacer>
                <MenuComponents.MenuButtonSpacer padding={20}></MenuComponents.MenuButtonSpacer>
                <MenuComponents.MenuButtonSpacer>
                  <BackButton
                  onClick={ backToShopQuery }>
                    Back
                  </BackButton>
                </MenuComponents.MenuButtonSpacer>
              </MenuComponents.MenuButtonContainer>
            </>
          : null
        }
      </MenuComponents.Container>
    </>
  )

}