import { Body, World, Bodies } from 'matter-js'
import * as PIXI from 'pixi.js'
import random from 'random'
import store from '../../../app/store'

let { getState } = store

export default function Rock(position) {
  let { gameObjects, TweenLite: { to, delayedCall } } = window
  let world = gameObjects.getKeyValue('world')
  let rocks = gameObjects.getKeyValue('rocks')
  let container = gameObjects.getKeyValue('game-pixi-container')
  let explosionAssetsSheet = gameObjects.getKeyValue('explosion-assets-sheet')
  let rockPieceAssets = [ 'rock-piece1.png', 'rock-piece2.png', 'rock-piece3.png' ]
  const getRandomRockSprite = () => (
    new PIXI.Sprite(
      explosionAssetsSheet.textures[
        rockPieceAssets[ Math.floor( Math.random() * rockPieceAssets.length ) ]
      ]
  ))
  let rockSprite = getRandomRockSprite()
  const rockScale = () => random.float(.45, .65)
  rockSprite.pivot.x = rockSprite.width * .48
  rockSprite.pivot.y = rockSprite.height * .48
  rockSprite.scale.x = rockScale()
  rockSprite.scale.y = rockScale()
  container.addChild(rockSprite)
  let rockBody = Bodies.rectangle(position.x, position.y - 50, 10, 10, {
    restitution: .25,
    collisionFilter: {
      category: 2,
      mask: 1
    }
  })
  Body.applyForce(rockBody, rockBody.position, {
    x: random.float(-0.005, 0.005),
    y: random.float(-0.001, -0.0025),
  })
  World.add(world, rockBody)
  this.animate = renderFn => {
    let { x, y } = renderFn(rockBody)
    rockSprite.position = { x, y }
  }
  this.destroy = () => {
    let idx = rocks.indexOf(this)
    World.remove(world, rockBody)
    container.removeChild(rockSprite)
    if (idx > -1) {
      rocks.splice(idx, 1)
    }
    world = null
    rocks = null
    container = null
    rockBody = null
    rockSprite = null
    gameObjects = null
  }
  delayedCall(
    getState().game.lastWaveCountDefeated &&
    getState().game.lastWaveCountDefeated < 10 ? 4 : 1,
    () => {
      to(rockSprite, .5, { alpha: 0, onComplete: () => { this.destroy() } })
    }
  )
  return this
}
