import React from 'react'
import { decrementWave, gameStateTransition, setWaveCountToDefeat, toggleZombieHealthDisplay, toggleFilterEffects, setZombiesOnStage, StatsDefs } from '../gameSlice'
import GameStates, { SubStates } from '../constants/GameStates'
import MenuComponents from './MenuComponents'
import store from '../../../app/store'
import { CornerBackButton, CustomMenuButton, Inline } from './ShopMenu'
import ReactTooltip from 'react-tooltip'
import { Children } from 'react'
import { batch, useDispatch, useSelector } from 'react-redux'
import { WEAPON_TYPE_PISTOL, WEAPON_TYPE_SHOTGUN, WEAPON_TYPE_SMG } from '../constants/weapons'
import updateScore from '../utils/updateScore'
import AchievementsView from './AchievementsView'
import { updateWeaponsUI } from '../ui/WeaponsUI'

const { Container, Backing, MenuTitle, MenuButtonContainer, MenuButtonSpacer } = MenuComponents

export const resetWave = () => {
  const screenUI = window.gameObjects.getKeyValue('screen-ui')
  const player = window.gameObjects.getKeyValue('player')
  // remove existing zombies, if any
  let zombies = window.gameObjects.getKeyValue('zombies')
  while(zombies.length > 0) {
    zombies.forEach(z => { z.kill(null, true) })
  }
  window.gameObjects.setKeyValue('zombies', [])
  screenUI.unpause()
  if (player.dead) {
    screenUI.respawn()
  }
}

export const gotoMainMenu = () => {
  const dispatch = store.dispatch
  resetWave()
  batch(() => {
    dispatch( setZombiesOnStage(0) )
    dispatch( setWaveCountToDefeat(null) )
    dispatch(gameStateTransition('sub', store.getState().game.gameSubState, SubStates.NEUTRAL))
    dispatch( gameStateTransition('main', store.getState().game.gameState, GameStates.MAIN_MENU))
  })
}

export const NoTransitionButton = ({ bgcolor, bordercolor, hoverborder, hovercolor, width, height, children }) => (
  <CustomMenuButton
  onMouseOver={ null }
  width={ width ? width : '200px' }
  height={ height ? height : '60px' }
  hovercolor={ hovercolor ? hovercolor : '#222' }
  hoverborder={ hoverborder ? hoverborder : '#333' }
  bordercolor={ bordercolor ? bordercolor : '#333' }
  bgcolor={ bgcolor ? bgcolor : '#222' }
  cancelTransition={true}
  >
    { children }
  </CustomMenuButton>
)

export const Stat = ({ label, stat }) => {
  return (
    <MenuButtonSpacer padding={ 1 }>
      <Inline>
        <NoTransitionButton
        width={ 200 }
        height={ 35 }
        bgcolor={ 'rgba(0, 0, 0, 0.75)' }
        hovercolor={ 'rgba(0, 0, 0, 0.75)' }
        bordercolor={ 'rgba(0, 0, 0, 0)' }
        hoverborder={ 'rgba(0, 0, 0, 0)' }
        >
          { label }
        </NoTransitionButton>
      </Inline>
      <Inline>
        <NoTransitionButton
        width={ 400 }
        height={ 35 }
        bgcolor={ 'rgba(0, 0, 0, 0.4)' }
        hovercolor={ 'rgba(0, 0, 0, 0.5)' }
        bordercolor={ 'rgba(0, 0, 0, 0)' }
        hoverborder={ 'rgba(0, 0, 0, 0)' }
        >
          { stat }
        </NoTransitionButton>
      </Inline>
    </MenuButtonSpacer>
  )
}

export default function PauseMenu (props) {

  const { gameObjects } = window
  const dispatch = useDispatch()
  const game = useSelector(state => state.game)
  const gameState = useSelector(state => state.game.gameState)
  const gameSubState = useSelector(state => state.game.gameSubState)
  const stats = useSelector(state => state.game.stats)
  const w = gameObjects.getKeyValue('game-width')
  const h = gameObjects.getKeyValue('game-height')
  const pauseMenuStates = {
    DEFAULT: 'Default',
    ACHIEVEMENTS: 'achievements',
    CONTROLS: 'Controls',
    OPTIONS: 'Options',
    STATS: 'Stats',
  }

  const showControls = () => {
    setPauseMenuState(pauseMenuStates.CONTROLS)
  }

  const showOptions = () => {
    setPauseMenuState(pauseMenuStates.OPTIONS)
  }

  const showDefault = () => {
    setPauseMenuState(pauseMenuStates.DEFAULT)
  }

  const seeScoreboard = () => {
    dispatch(
      gameStateTransition('main', GameStates.MAIN_MENU, GameStates.SCORES_VIEW)
    )
  }

  const showStats = () => {
    setPauseMenuState(pauseMenuStates.STATS)
  }

  const [pauseMenuState, setPauseMenuState] = React.useState(pauseMenuStates.DEFAULT)

  const resumeGame = () => {
    if (gameState === GameStates.PAUSE) {
      window.gameObjects.getKeyValue('screen-ui').togglePause()
    }
  }

  const playGame = () => {
    dispatch(gameStateTransition('main', gameState, GameStates.GAMEPLAY))
    dispatch(gameStateTransition('sub', gameSubState, SubStates.WAITING))
    setTimeout(() => {
      let weaponsUI = window.gameObjects.getKeyValue('weapons-ui')
      weaponsUI.updateGrenadeIcons()
      updateWeaponsUI()
      weaponsUI.updateWeaponMagazines(game.player.weapons.equipped)
      let screenUI = window.gameObjects.getKeyValue('screen-ui')
      screenUI.unpause()
    }, 100)
  }

  const showAchievements = () => {
    setPauseMenuState(pauseMenuStates.ACHIEVEMENTS)
  }

  const effectFilters = useSelector(state => state.game.filterEffects)

  const toggleFilters = () => {
    dispatch( toggleFilterEffects(!effectFilters) )
    let zombies = window.gameObjects.getKeyValue('zombies')
    zombies.forEach(zombie => zombie.checkFilters())
    let ammos = window.gameObjects.getKeyValue('ammo-drops-by-type')
    ammos[WEAPON_TYPE_SMG]
    .concat(ammos[WEAPON_TYPE_PISTOL])
    .concat(ammos[WEAPON_TYPE_SHOTGUN])
    .forEach(ammo => ammo.checkFilters())
    let credits = window.gameObjects.getKeyValue('currency-drops')
    credits.forEach(credit => credit.checkFilters())
    let healths = window.gameObjects.getKeyValue('health-drops')
    healths.forEach(health => health.checkFilters())
    let player = window.gameObjects.getKeyValue('player')
    player.checkFilters()
    let weaponsUI = window.gameObjects.getKeyValue('weapons-ui')
    weaponsUI.checkFilters()
  }

  const displayZombieHealth = useSelector(state => state.game.displayZombieHealth)

  const toggleZombieHealth = () => {
    dispatch( toggleZombieHealthDisplay(!displayZombieHealth) )
    let zombies = window.gameObjects.getKeyValue('zombies')
    zombies.forEach(zombie => {
      zombie.displayHealth(!displayZombieHealth)
    })
  }

  return (
    <>
    {
      gameObjects ?
        <Container style={{ top: props.margintop ? '100px' : 0 }} width={ w * .75 } height={ props['shop-query-visibility'] ? h * .5 : h * .75 }>
          { 
            props.hidebg ? null :
            <>
              <Backing bgcolor={ '#222' } opacity={ gameState === GameStates.MAIN_MENU ? .35 : .5 } width={ w * .75 } height={ props['shop-query-visibility'] ? h * .5 : h * .75 }/>
            </>
          }
          {
            pauseMenuState === pauseMenuStates.DEFAULT ?
            <>
              {
                props.hidetitle ? null
                : <MenuTitle>Pause Menu</MenuTitle>
              }
              <MenuButtonContainer width={ w ? w : 0 }>
                <MenuButtonSpacer padding={ gameState === GameStates.PAUSE ? 4 : 6 }>
                  {
                    props.playbtn ?
                      <CustomMenuButton
                      height={ 40 }
                      onClick={ playGame }>
                        Play Game
                      </CustomMenuButton>
                    :
                      <CustomMenuButton
                      height={40}
                      onClick={ resumeGame }>
                        Resume Game
                      </CustomMenuButton>
                  }
                </MenuButtonSpacer>
                {
                  gameState !== GameStates.MAIN_MENU ?
                    <MenuButtonSpacer padding={4}>
                      <CustomMenuButton
                      height={40}
                      onClick={ gotoMainMenu }>
                        Main Menu
                      </CustomMenuButton>
                    </MenuButtonSpacer>
                  :
                    <MenuButtonSpacer padding={2}>
                      <CustomMenuButton
                      height={40}
                      onClick={ seeScoreboard }>
                        Scoreboard
                      </CustomMenuButton>
                    </MenuButtonSpacer>
                }
                <MenuButtonSpacer padding={ gameState === GameStates.PAUSE ? 4 : 4 }>
                  <CustomMenuButton
                  height={40}
                  onClick={ showControls }>
                    View Controls
                  </CustomMenuButton>
                </MenuButtonSpacer>
                <MenuButtonSpacer padding={ gameState === GameStates.PAUSE ? 4 : 4 }>
                  <CustomMenuButton
                  height={40}
                  onClick={ showOptions }>
                    Options
                  </CustomMenuButton>
                </MenuButtonSpacer>
                <MenuButtonSpacer padding={ gameState === GameStates.PAUSE ? 1 : 4 }>
                  <CustomMenuButton
                  height={40}
                  onClick={ showStats }>
                    Stats
                  </CustomMenuButton>
                </MenuButtonSpacer>
                <MenuButtonSpacer padding={ gameState === GameStates.PAUSE ? 1 : 4 }>
                  <CustomMenuButton
                  height={40}
                  onClick={ showAchievements }>
                    Achievements
                  </CustomMenuButton>
                </MenuButtonSpacer>
              </MenuButtonContainer>
            </>
            : null
          }
          {
            pauseMenuState === pauseMenuStates.ACHIEVEMENTS ?
            <AchievementsView backBtnFn={ showDefault } /> : null
          }
          {
            pauseMenuState === pauseMenuStates.CONTROLS ?
            <>
              <MenuTitle>Controls</MenuTitle>
              <MenuButtonContainer width={ w }>
                <CornerBackButton marginTop={'-60px'} onClick={ showDefault } />
                <Inline>
                  <CustomMenuButton hovercolor={'#222'} hoverborder={'#333'} cancelTransition={true} height={50} width={360}>Jump: W</CustomMenuButton>
                  <CustomMenuButton hovercolor={'#222'} hoverborder={'#333'} cancelTransition={true} height={50} width={360}>Run Left: A</CustomMenuButton>
                  <CustomMenuButton hovercolor={'#222'} hoverborder={'#333'} cancelTransition={true} height={50} width={360}>Run Right: D</CustomMenuButton>
                  <CustomMenuButton hovercolor={'#222'} hoverborder={'#333'} cancelTransition={true} height={50} width={360}>Melee Weapon Bash: F</CustomMenuButton>
                  <CustomMenuButton hovercolor={'#222'} hoverborder={'#333'} cancelTransition={true} height={50} width={360}>Aim: Mouse</CustomMenuButton>
                  <CustomMenuButton hovercolor={'#222'} hoverborder={'#333'} cancelTransition={true} height={50} width={360}>Shoot: Left Mouse</CustomMenuButton>
                </Inline>
                <Inline>
                  <CustomMenuButton hovercolor={'#222'} hoverborder={'#333'} cancelTransition={true} height={50} width={360}>Charge Grenade Throw: Hold G / Right Mouse</CustomMenuButton>
                  <CustomMenuButton hovercolor={'#222'} hoverborder={'#333'} cancelTransition={true} height={50} width={360}>Change Weapon: Space / Mouse Wheel</CustomMenuButton>
                  <CustomMenuButton hovercolor={'#222'} hoverborder={'#333'} cancelTransition={true} height={50} width={360}>Turn Turret: L/R Arrow (while placing)</CustomMenuButton>
                  <CustomMenuButton hovercolor={'#222'} hoverborder={'#333'} cancelTransition={true} height={50} width={360}>Detonate remote mines: Numpad  1 - 9</CustomMenuButton>
                </Inline>
              </MenuButtonContainer>
            </>
            : null
          }
          {
            pauseMenuState === pauseMenuStates.OPTIONS ?
            <>
              <MenuTitle>Options</MenuTitle>
              <MenuButtonContainer>
                <CornerBackButton marginTop={'-60px'} onClick={ showDefault } />
                <MenuButtonSpacer>
                  <Inline>
                    <MenuButtonSpacer padding={4}>
                      <NoTransitionButton
                      width={ 240 }
                      height={ 60 }
                      >Effect Filters?</NoTransitionButton>
                    </MenuButtonSpacer>
                  </Inline>
                  <Inline>
                    <MenuButtonSpacer padding={4} data-tip data-for='effects-filter'>
                      <CustomMenuButton
                      width={ 100 }
                      hoverborder={ effectFilters ? 'white' : 'red' }
                      onClick={ toggleFilters }>
                        <span
                        style={{ color: effectFilters ? 'white' : 'red' }}>
                        { effectFilters ? 'On' : 'Off' }
                        </span>
                      </CustomMenuButton>
                    </MenuButtonSpacer>
                    <ReactTooltip id='effects-filter'>
                      <span >
                        Adds filter effects like glow and motion blur to animations. Turning off could improve performance.
                      </span>
                    </ReactTooltip>
                  </Inline>
                </MenuButtonSpacer>
                <MenuButtonSpacer>
                  <Inline>
                    <MenuButtonSpacer padding={4}>
                      <NoTransitionButton
                      width={ 240 }
                      height={ 60 }
                      >Display Zombie Health?</NoTransitionButton>
                    </MenuButtonSpacer>
                  </Inline>
                  <Inline>
                    <MenuButtonSpacer padding={4} data-tip data-for='display-zombie-health'>
                      <CustomMenuButton
                      width={ 100 }
                      hoverborder={ displayZombieHealth ? 'white' : 'red' }
                      onClick={ toggleZombieHealth }>
                        <span
                        style={{ color: displayZombieHealth ? 'white' : 'red' }}>
                        { displayZombieHealth ? 'On' : 'Off' }
                        </span>
                      </CustomMenuButton>
                    </MenuButtonSpacer>
                    <ReactTooltip id='display-zombie-health'>
                      <span >
                        Displays a yellow text box above each enemy zombie
                      </span>
                    </ReactTooltip>
                  </Inline>
                </MenuButtonSpacer>
              </MenuButtonContainer>
            </>
            : null
          }
          {
            pauseMenuState === pauseMenuStates.STATS ?
            <>
              <MenuTitle>Stats</MenuTitle>
              <MenuButtonContainer>
                <CornerBackButton marginTop={'-60px'} onClick={ showDefault } />
                <Stat label={ 'Shots Fired' } stat={ stats.bulletsFired } />
                <Stat label={ 'Mags Changed' } stat={ stats.timesReloaded } />
                <Stat label={ 'Grenades Thrown' } stat={ stats.grenadesThrown } />
                <Stat label={ 'Zombies Killed' } stat={ stats.zombiesKilled } />
                <Stat label={ 'Deaths' } stat={ stats.timesKilled } />
                {/* <Stat label={ 'Respawns' } stat={ stats.timesRespawned } /> */}
                
              </MenuButtonContainer>
            </>
            : null
          }
        </Container>
      : null
    }
    </>
  )
}
