import React from 'react'
import { useSelector, useDispatch, batch } from 'react-redux'
import { SubStates } from '../constants/GameStates'
import { addPurchase, decrementWave, setZombiesOnStage, gameStateTransition, setWaveCountToDefeat, subtractFromTotalCredits } from '../gameSlice'
import MenuComponents from './MenuComponents'
import { gotoMainMenu, resetWave } from './PauseMenu'
import { CustomMenuButton } from './ShopMenu'

export default () => {
  
  const { gameObjects } = window
  const dispatch = useDispatch()
  const gameSubState = useSelector(state => state.game.gameSubState)
  const lastGameSubState = useSelector(state => state.game.lastGameSubState)
  const credits = useSelector(state => state.game.player.credits)
  const respawnCost = useSelector(state => state.game.respawnCost)
  const respawnToShopCost = useSelector(state => state.game.respawnToShopCost)
  const waveNum = useSelector(state => state.game.waveNum)
  const width = gameObjects.getKeyValue('game-width')
  const height = gameObjects.getKeyValue('game-height')
  const screenUI = gameObjects.getKeyValue('screen-ui')

  return (
    <>
      <MenuComponents.Container width={width * .75} height={height * .75}>
        <MenuComponents.Backing width={width * .75} height={height * .75}></MenuComponents.Backing>
        <MenuComponents.MenuTitle>
          {
            credits >= respawnToShopCost ? 'Respawn Menu'
            : 'Game Over'
          }
        </MenuComponents.MenuTitle>
        <MenuComponents.MenuButtonContainer>
          {
            credits >= respawnCost || lastGameSubState === SubStates.SHOP_QUERY_SUBMENU ?
            <MenuComponents.MenuButtonSpacer>
              <CustomMenuButton
              width={240}
              onClick={() => {
                screenUI.respawn()
                if (lastGameSubState !== SubStates.SHOP_QUERY_SUBMENU && lastGameSubState !== SubStates.PLAYER_RESPAWN && lastGameSubState !== SubStates.WAVE_STARTED) {
                  dispatch( gameStateTransition('sub', gameSubState, SubStates.WAITING) )
                }
                if (lastGameSubState !== SubStates.SHOP_QUERY_SUBMENU) {
                  dispatch( subtractFromTotalCredits(respawnCost) )
                }
                dispatch( addPurchase('Respawn', respawnCost) )
              }}>
                Respawn? { lastGameSubState === SubStates.SHOP_QUERY_SUBMENU ? '' : `$${respawnCost}` }
              </CustomMenuButton>
            </MenuComponents.MenuButtonSpacer>
            : null
          }
          {
            credits >= respawnToShopCost && waveNum > 0 && lastGameSubState !== SubStates.SHOP_QUERY_SUBMENU ?
            <MenuComponents.MenuButtonSpacer>
              <CustomMenuButton
              width={360}
              onClick={() => {
                resetWave()
                batch(() => {
                  dispatch( setZombiesOnStage(0) )
                  dispatch( setWaveCountToDefeat(null) )
                  dispatch( decrementWave() )
                  dispatch( gameStateTransition('sub', gameSubState, SubStates.SHOP_QUERY_SUBMENU) )
                  dispatch( subtractFromTotalCredits(respawnToShopCost) )
                  dispatch( addPurchase('Shop Respawn', respawnToShopCost) )
                })
              }}>
                Restart Current Wave from Shop? { `$${respawnToShopCost}` }
              </CustomMenuButton>
            </MenuComponents.MenuButtonSpacer>
            : null
          }
          <MenuComponents.MenuButtonSpacer>
            <CustomMenuButton
            onClick={gotoMainMenu}>
              Go to Main Menu
            </CustomMenuButton>
          </MenuComponents.MenuButtonSpacer>
        </MenuComponents.MenuButtonContainer>
      </MenuComponents.Container>
    </>
  )
}