import { createSlice } from '@reduxjs/toolkit'
import GameStates, { SubStates } from './constants/GameStates'
import { WEAPON_TYPE_SMG, WEAPON_TYPE_PISTOL, WEAPON_TYPE_SHOTGUN, WEAPON_TYPE_GRENADE } from './constants/weapons'
// import { original } from 'immer'
import initialState from '../../app/initialState'

export const AchievementDefs = {
  THIS_IS_SPARTA: 'SPARTA',
  FROM_DOWNTOWN: 'Downtown',
  FIRE_IN_THE_HOLE: 'Triplets',
  WEAPONS_COLLECTOR: 'Collector',
  PISTOL_MODDER: 'P.Modder',
  SMG_MODDER: 'SMG Modder',
  SHOTGUN_MODDER: 'S.Modder',
  MOD_AFICIONADO: 'Aficionado',
  THE_JUMP_SHOT: 'Jumpshot',
  PILEDRIVE: 'Piledrive',
  TRAPHOUSE: 'Traphouse',
  BARRICADE: 'Barricade',
  MINER: 'Miner',
  GUNNA: 'Gunna',
}

export const StatsDefs = {
  zombiesKilled: 'zombiesKilled',
  bulletsFired: 'bulletsFired',
  timesReloaded: 'timesReloaded',
  grenadesThrown: 'grenadesThrown',
  timesKilled: 'timesKilled',
  timesRespawned: 'timesRespawned',
  itemsPickedUp: 'itemsPickedUp',
  creditsEarned: 'creditsEarned',
  storePurchases: 'storePurchases',
  meleeKills: 'meleeKills',
  mineKills: 'mineKills',
  trapsPlaced: 'trapsPlaced',
  barriersPlaced: 'barriersPlaced',
  turretKills: 'turretKills',
  damageTaken: 'damageTaken',
  damageDealt: 'damageDealt',
}

// for flattened data game state
export const gameSlice = createSlice({
	name: 'game',
  // initialState: {},
  initialState: initialState, //JSON.parse(localStorage.getItem('ZombieChamber-GameSave')) || ,
	reducers: {
    loadLocalStorageState: state => {
      if (localStorage && localStorage.getItem('ZombieChamber-GameSave')) {
        return JSON.parse(localStorage.getItem('ZombieChamber-GameSave'))
      } else {
        return state
      }
    },
    setTrapTypePlaced: (state, action) => ({
      ...state,
      trapTypesPlaced: {
        ...state.trapTypesPlaced,
        [ action.payload ]: state.trapTypesPlaced[ action.payload ] ? state.trapTypesPlaced[ action.payload ] + 1 : 1
      },
    }),
    setBarrierTypePlaced: (state, action) => ({
      ...state,
      barrierTypesPlaced: {
        ...state.barrierTypesPlaced,
        [ action.payload ]: state.barrierTypesPlaced[ action.payload ] ? state.barrierTypesPlaced[ action.payload ] + 1 : 1
      }
    }),
    setLastMagDropped: (state, action) => {
      state.lastMagDropped = action.payload
    },
    increaseStats: (state, action) => {
      state.stats[ action.payload ] += 1
    },
    increaseStatsByAmount: {
      reducer: (state, action) => {
        state.stats[ action.payload.type ] += action.payload.amount
      },
      prepare: (type, amount) => ({ payload: { type, amount } })
    },
    setLastGrenadeKill: (state, action) => {
      state.lastGrenadeKill = action.payload
      state.consecutiveGrenadeKills += 1
    },
    resetConsecutiveGrenadeKills: state => { state.consecutiveGrenadeKills = 0 },
    setZombieLastKilled: (state, action) => {
      state.zombieLastKilled = action.payload
    },
    flipScoreToggle: state => { state.scoreToggle = !state.scoreToggle },
    increaseScore: (state, action) => {
      state.score += action.payload
    },
    decreaseScore: (state, action) => {
      state.score -= action.payload
    },
    updatePrevScore: (state) => {
      state.prevScore = state.score
    },
    addPurchase: {
      reducer: (state, action) => {
        state.purchases += 1
        state.lastPurchase = action.payload
      },
      prepare: (item, amount) => ({ payload: { item, amount } })
    },
    setZombiesOnLeft: (state, action) => { state.enemies.zombies.onLeft = action.payload },
    setZombiesOnRight: (state, action) => { state.enemies.zombies.onRight = action.payload },
    setRightMouseDown: (state, action) => {
      state.rightMouseDown = action.payload
    },
    attachMod: {
      reducer: (state, action) => {
        let { modType, wepType } = action.payload
        switch (modType) {
          case 'rof':
          state.player.modTiers.rof[wepType].current += 1
          state.player.weapons.shotLimiter[wepType] -= state.player.modAmounts.rof[wepType]
          break;
          case 'powder':
          state.player.modTiers.powder[wepType].current += 1
          state.player.weapons.damage[wepType].min += Math.round(state.player.modAmounts.powder[wepType] * .5)
          state.player.weapons.damage[wepType].max += Math.round(state.player.modAmounts.powder[wepType])
          break;
          case 'sight':
          state.player.modTiers.sight[wepType].current += 1
          state.player.weapons.bulletVariance[wepType] -= state.player.modAmounts.sight[wepType]
          break;
          case 'mag':
          state.player.modTiers.mag[wepType].current += 1
          state.player.weapons.ammoCapacity[wepType] += state.player.modAmounts.mag[wepType]
          break;
          default:
        }
      },
      prepare: (modType, wepType) => ({ payload: { modType, wepType } })
    },
    setPlayerCompositeId: (state, action) => { console.log(action.type); state.player.compositeId = action.payload },
		setKeyPress: (state, action) => { state.keys.push(action.payload) },
		removeKeyPress: (state, action) => { state.keys.splice(state.keys.indexOf(parseInt(action.payload)), 1) },
		setLastDirection: (state, action) => { state.player.variables.lastDirection = action.payload },
		setCurrentDirection: (state, action) => { state.player.variables.currentDirection = action.payload },
		setJumping: (state, action) => { state.player.variables.jumping = action.payload },
		setGroundId: (state, action) => { state.groundId = action.payload },
		setMousePosition: (state, action) => { state.mousePosition = action.payload },
		setMouseStateX: (state, action) => { state.mouseState.x = action.payload },
		setMouseStateY: (state, action) => { state.mouseState.y = action.payload },
		setMouseButtonState: (state, action) => { state.mouseState.btn = action.payload },
		setWeaponEquipped: (state, action) => { state.player.weapons.equipped = action.payload },
		setLastShot: {
			reducer: (state, action) => {
				let { player: { weapons: { lastShot, equipped } } } = state
				lastShot[equipped] = action.payload.dateTimeObject
			},
			prepare: (weapon, dateTimeObject) => {
				return {
					payload: { weapon, dateTimeObject }
				}
			}
		},
		setMouseRelativeToPlayerPosition: (state, action) => { state.mouseRelativeToPlayerPosition = action.payload },
		setCompositeBodyIds: (state, action) => {
			state.player.composite = action.payload
		},
		setStaticGunAngle: (state, action) => { state.staticGunAngle = action.payload },
		setStaticGunAngleState: (state, action) => { state.staticGun = action.payload },
		setTexturePath: {
			reducer: (state, action) => {
				state.textures[action.payload.textureName] = action.payload.texturePath
			},
			prepare: (textureName, texturePath) => {
				return {
					payload: {
						textureName,
						texturePath
					}
				}
			}
		},
		gameStateTransition: {
			reducer: (state, action) => {
				switch (action.payload.type) {
					case 'sub':
					state.lastGameSubState = action.payload.from
					state.gameSubState = action.payload.to
						break;
					case 'main':
					state.lastGameState = action.payload.from
					state.gameState = action.payload.to
						break;
					default:

				}
			},
			prepare: (type, from, to) => ({ payload: { type, from, to } })
		},
		incrementWave: (state) => { state.waveNum += 1 },
		decrementWave: (state) => { state.waveNum -= 1 },
		setWaveCountToDefeat: (state, action) => { state.waveCountToDefeat = action.payload },
		incrementWaveCountToDefeat: (state, action) => { state.waveCountToDefeat += 1 },
		decrementWaveCountToDefeat: (state, action) => {
      state.waveCountToDefeat -= 1
      state.zombiesOnStage -= 1
    },
		setWeaponsList: (state, action) => { state.player.weapons.list = action.payload },
		setShowingMessage: (state, action) => { state.showingMessage = action.payload },
		unsetAllKeys: (state, action) => { state.keys = [] },
		resetPlayerVariables: state => {
			let _state = Object.assign({
				variables: {
					...state,
					currentDirection: '',
					lastDirection: '',
					jumping: false
				}
      }, state)
      state = _state
		},
		decrementTotalAmmo: (state, action) => {
			let { player: { weapons: { totalAmmo } } } = state
			// original() from the immer package helps us to see values in redux arrays/objects
			totalAmmo[action.payload] -= 1
    },
    addToTotalAmmo: {
      reducer: (state, action) => {
        let { player: { weapons: { totalAmmo } } } = state
        totalAmmo[action.payload.wepType] += action.payload.amount
      },
      prepare: (wepType, amount) => ({ payload: { wepType, amount } })
    },
    updateAmmoCapacity: {
      reducer: (state, action) => {
        let { player: { weapons: { ammoCapacity } } } = state
        ammoCapacity[action.payload.wepType] = action.payload.newCapacityAmount
      },
      prepare: (wepType, newCapacityAmount) => ({ payload: { wepType, newCapacityAmount } })
    },
    setPlayerPosition: (state, action) => { state.player.position = action.payload },
    addToTotalCredits: (state, action) => { state.player.credits += action.payload },
    subtractFromTotalCredits: (state, action) => { state.player.credits -= action.payload },
    upgradeRunSpeed: (state) => {
      state.player.upgradeTiers.run.current += 1
      state.player.variables.acceleration.max = state.player.variables.acceleration.max + state.player.upgradeAmounts.run
    },
    upgradeReloadSpeed: (state) => {
      state.player.upgradeTiers.reload.current += 1
      state.player.weapons.reloadSpeed[WEAPON_TYPE_PISTOL] = state.player.weapons.reloadSpeed[WEAPON_TYPE_PISTOL] + state.player.upgradeAmounts.reload
      state.player.weapons.reloadSpeed[WEAPON_TYPE_SMG] = state.player.weapons.reloadSpeed[WEAPON_TYPE_SMG] + state.player.upgradeAmounts.reload
    },
    upgradeJumpForce: (state) => {
      state.player.upgradeTiers.jump.current += 1
      state.player.variables.jumpForce = state.player.variables.jumpForce + state.player.upgradeAmounts.jump
    },
    upgradeDefense: (state) => {
      state.player.upgradeTiers.defense.current += 1
      state.player.variables.defense = state.player.variables.defense + state.player.upgradeAmounts.defense
    },
    upgradeMelee: (state) => {
      state.player.upgradeTiers.melee.current += 1
      state.player.weapons.meleeDamage[WEAPON_TYPE_PISTOL] += state.player.upgradeAmounts.melee[WEAPON_TYPE_PISTOL]
      state.player.weapons.meleeDamage[WEAPON_TYPE_SMG] += state.player.upgradeAmounts.melee[WEAPON_TYPE_SMG]
    },
    updatePlayerHealth: (state, action) => {
      state.player.health = action.payload
    },
    decrementGrenadeCount: (state) => { state.player.weapons.totalAmmo['grenades'] -= 1 },
    incrementGrenadeCount: (state) => { state.player.weapons.totalAmmo['grenades'] += 1 },
    updateZombieWalkSpeed: (state, action) => {
      let walkSpeedDefault = state.enemies.zombies.walkSpeedDefault
      let incrementer = state.enemies.zombies.walkSpeedIncrementer
      state.enemies.zombies.walkSpeed = walkSpeedDefault + (action.payload * incrementer)
    },
    updateZombieAttackDmg: (state, action) => {
      state.enemies.zombies.attackDmg = Math.round(
        (action.payload * state.enemies.zombies.attackIncrementer) +
        state.enemies.zombies.attackDmgDefault
      )
    },
    decrementZombieAttackTimeLimit: (state, action) => {
      state.enemies.zombies.zombieAttackLimiter = state.enemies.zombies.zombieAttackDefaultLimiter - (action.payload * state.enemies.zombies.zombieAttackLimiterDecrementer)
      console.log(
        state.enemies.zombies.zombieAttackLimiter
      )
    },
    showLaserSight: (state, action) => {
      state.player.weapons.laser[action.payload] = true
    },
    incrementZombieHealth: (state, action) => {
      state.enemies.zombies.startingHealth = Math.round(state.enemies.zombies.defaultHealth + (state.enemies.zombies.healthIncrementer * action.payload))
    },
    setSelectedBarrierToBuild: (state, action) => {
      state.barriers.selectedBarrier = action.payload
    },
    setSelectedTrapToBuild: (state, action) => {
      state.traps.selectedTrap = action.payload
    },
    setLastWaveCountDefeated: (state) => { state.lastWaveCountDefeated = state.waveCountToDefeat },
    incrementZombiesOnStage: state => { state.zombiesOnStage += 1 },
    setZombiesOnStage: (state, action) => { state.zombiesOnStage = action.payload },
    updateWeaponsList: (state, action) => {
      state.player.weapons.list.push(action.payload)
    },
    // Perks
    setPlayerMagnetism: {
      reducer: (state, action) => {
        state.player.variables.magnetism = action.payload.range
        state.player.variables.magnetismSpeed = action.payload.speed
        state.player.perks.magnetism.owned = true
      },
      prepare: (range, speed) => ({ payload: { range, speed } })
    },
    setPlayerMagDamage: (state) => {
      state.player.perks.magDmg.owned = true
    },
    setPlayerLastBullet: (state) => {
      state.player.perks.lastBullet.owned = true
    },
    setPlayerCreditDropper: state => { state.player.perks.creditDropper.owned = true },
    setPlayerAmmoDropper: state => { state.player.perks.ammoDropper.owned = true },
    setPlayerHealthDropper: state => { state.player.perks.healthDropper.owned = true },
    setPlayerQuickTurret: state => {
      state.player.perks.quickTurret.owned = true
      state.traps.turret.cooldown = .25
      state.traps.turret.fireDistance = 600
    },
    setPlayerBarrierPlus: state => {
      state.player.perks.barrierPlus.owned = true
    },
    setPlayerFusterCluck: state => {
      state.player.perks.cluck.owned = true
    },
    toggleFilterEffects: (state, action) => {
      state.filterEffects = action.payload
    },
    toggleZombieHealthDisplay: (state, action) => {
      state.displayZombieHealth = action.payload
    },
    setAchievementOwned: (state, action) => {
      state.achievements[action.payload].owned = true
      state.lastAchievementEarned = action.payload
    }
	},
})

export const {
  loadLocalStorageState,
  increaseStatsByAmount,
  setTrapTypePlaced,
  setBarrierTypePlaced,
  setLastMagDropped,
  increaseStats,
  setAchievementOwned,
  resetConsecutiveGrenadeKills,
  setLastGrenadeKill,
  setZombieLastKilled,
  flipScoreToggle,
  updatePrevScore,
  increaseScore,
  decreaseScore,
  toggleZombieHealthDisplay,
  toggleFilterEffects,
  setPlayerFusterCluck,
  setPlayerBarrierPlus,
  setPlayerQuickTurret,
  setPlayerHealthDropper,
  setPlayerAmmoDropper,
  setPlayerCreditDropper,
  setPlayerLastBullet,
  setPlayerMagDamage,
  setPlayerMagnetism,
  addPurchase,
  setZombiesOnLeft,
  setZombiesOnRight,
  setRightMouseDown,
	resetPlayerVariables,
	unsetAllKeys,
	setPlayerCompositeId,
	setKeyPress, removeKeyPress,
	setLastDirection, setCurrentDirection,
	setJumping, setGroundId, setMousePosition,
	setMouseStateX, setMouseStateY, setMouseButtonState,
	setLastShot,
	setWeaponEquipped,
	setMouseRelativeToPlayerPosition,
	setCompositeBodyIds,
	setTexturePath,
	gameStateTransition,
  incrementWave,
  decrementWave,
  incrementWaveCountToDefeat,
	setWaveCountToDefeat,
	decrementWaveCountToDefeat,
	setWeaponsList,
	setShowingMessage,
  decrementTotalAmmo,
  addToTotalAmmo,
  updateAmmoCapacity,
  addToTotalCredits,
  subtractFromTotalCredits,
  setPlayerPosition,
  upgradeRunSpeed,
  upgradeReloadSpeed,
  upgradeJumpForce,
  upgradeDefense,
  upgradeMelee,
  updatePlayerHealth,
  attachMod,
  incrementGrenadeCount,
  decrementGrenadeCount,
  updateZombieWalkSpeed,
  updateZombieAttackDmg,
  decrementZombieAttackTimeLimit,
  showLaserSight,
  incrementZombieHealth,
  setSelectedBarrierToBuild,
  setSelectedTrapToBuild,
  lastWaveCountDefeated,
  incrementZombiesOnStage,
  setZombiesOnStage,
  updateWeaponsList
} = gameSlice.actions

export default gameSlice.reducer
