import { AnimatedSprite, Container, Text } from 'pixi.js'
import { Bodies, World, Constraint } from 'matter-js'
import GrenadeExplosion from '../FX/GrenadeExplosion'
import { PistolShell, PistolBullet } from '../player/fireWeapons'
import { radians, degrees } from 'radians'
import { getBodyHeight, getBodyWidth } from '../utils/Utils'
import store from '../../../app/store'
import MuzzleFlashFX from '../FX/MuzzleFlashFX'
import random from 'random'
import { playerCollisionOptions } from '../collisionOptions'

export default class MachineGunTurret {

  constructor(position) {
    let {x,y} = position
    this.delayedCall = window.TweenLite.delayedCall
    this.gameObjects = window.gameObjects
    this.world = this.gameObjects.getKeyValue('world')
    this.buildObjects = this.gameObjects.getKeyValue('build-objects')
    this.container = this.gameObjects.getKeyValue('game-pixi-container')
    this.scanAnim = new AnimatedSprite(
      this.gameObjects.getKeyValue('turret-scan-sheet').animations['turretScan']
    )
    this.scanAnim.visible = true
    this.scanAnim.loop = true
    this.scanAnim.play()
    this.shootAnim = new AnimatedSprite(
      this.gameObjects.getKeyValue('turret-shoot-sheet').animations['turretShoot']
    )
    this.shootAnim.loop = false
    this.shootAnim.onFrameChange = () => {
      // if (this.shootAnim.currentFrame === 2) {
      //   this.fireBullet()
      // }
    }
    this.shootAnim.onComplete = () => {
      this.shootAnim.gotoAndStop(0)
      this.scanAnim.play()
      this.scanAnim.visible = true
      this.shootAnim.visible = false
    }
    this.shootAnim.visible = false
    this.scanAnim.width = 150
    this.scanAnim.height = 150
    this.scanAnim.pivot.x = this.scanAnim.width * .5
    this.scanAnim.pivot.y = this.scanAnim.height
    this.shootAnim.width = 150
    this.shootAnim.height = 150
    this.shootAnim.pivot.x = this.shootAnim.width * .5
    this.shootAnim.pivot.y = this.shootAnim.height
    this.turret = new Container()
    this.turret.addChild(this.scanAnim, this.shootAnim)
    this.container.addChild(this.turret)

    this.turret.children.forEach(child => {
    })

    this.body = Bodies.rectangle(x, y, 50, 50, {
      label: 'turret barrier',
      main: this
    })
    this.body.removeSelf = () => {
      this.destroy()
    }
    let ground = this.gameObjects.getKeyValue('ground')
    this.constraint = Constraint.create({
      bodyA: ground,
      bodyB: this.body,
      pointA: { x: -getBodyWidth(ground)/2 + position.x, y: -getBodyHeight(ground)/2 },
      pointB: { x: 0, y: getBodyHeight(this.body)/2 },
      length: 0
    })
    World.add(this.world, [this.body, this.constraint])
    
    this.body.strength = 250
    this.healthDisplay = new Text(`health: ${this.body.strength}`, { fontSize: 12, fill: 0xFFFFFF })
    this.healthDisplay.pivot.x = this.healthDisplay.width * .5
    this.container.addChild(this.healthDisplay)

    this.direction = 'right'

    // re-add player sprites on-top
    let player = this.gameObjects.getKeyValue('player')
    if (!player.dead) {
      player.sprites.forEach(s => { this.container.addChild(s) })
    }

    this.destroyed = false

    return this
  }

  takeDamage(dmg) {
    this.body.strength -= dmg
    this.healthDisplay.text = `health: ${this.body.strength}`
  }

  displayBarrierStrength(renderFn) {
    let {x,y} = renderFn(this.body)
    this.healthDisplay.position = { x, y: y - 100 }
    this.turret.position = { x, y }
  }

  updateDirection(direction) {
    this.turret.scale.x = direction === 'left' ? -1 : 1
    this.direction = direction
  }

  getBody() {
    return this.body
  }

  resumeScan() {
    if (!this.scanning) {
      this.scanning = true
      this.scanAnim.play()
      this.scanAnim.visible = true
      this.shootAnim.visible = false
    }
  }

  fireBullet() {
    if (!this.destroyed) {
      if (!this.muzzleFlashFX) {
        this.muzzleFlashFX = new MuzzleFlashFX()
        this.muzzleFlashFX.setMuzzleRotations(this.direction === 'left' ? degrees(-92) : degrees(92))
      }
      // fire bullet
      let { game: { player: { weapons: { bulletForce } } } } = store.getState()
      let { x, y } = this.body.position
      this.muzzleFlashFX.fireEffect(
        this.direction == 'left'
        ? { x: this.turret.position.x - 100, y: this.turret.position.y - 25 }
        : { x: this.turret.position.x + 100, y: this.turret.position.y - 25  }
      )
      let overrideBody = Bodies.circle(x, y - 25, 5)
      let _ejectY = random.float(-0.0005, -0.00075)
      new PistolShell(0, 0, this.gameObjects, null, {
        body: Bodies.rectangle(x, y - 50, 7, 4, { collisionFilter: playerCollisionOptions }),
        force: this.direction === 'right' ?
        {
          "x": random.float(-0.00035, -0.00075),
          "y": _ejectY
        } : {
          "x": random.float(0.00035, 0.00075),
          "y": _ejectY
        },
      })
      new PistolBullet(0, 0, this.gameObjects, 'turret', {
        overrideBody,
        overrideForce: this.direction === 'left' ?
        {
          "x": -0.01,
          "y": -0.00075
        } : {
          "x": 0.01,
          "y": -0.00075
        },
        overrideAngle: this.direction === 'left' ? -3.0 : 0.1
      })
    }
  }

  shoot() {
    if (!this.shooting && !this.destroyed) {
      let { game: { traps: { turret: { cooldown } } } } = store.getState()
      this.fireBullet()
      this.shooting = true
      this.scanAnim.visible = false
      this.shootAnim.visible = true
      this.shootAnim.play()
      this.delayedCall(cooldown, () => {
        this.shooting = false
      })
    }
  }

  destroy() {
    if (!this.destroyed) {
      this.destroyed = true
      new GrenadeExplosion(this.body.position, true)
      this.container.removeChild(this.turret, this.healthDisplay)
      World.remove(this.world, [this.body, this.constraint])
      let idx = this.buildObjects.indexOf(this, 1)
      if (idx > -1) {
        this.buildObjects.splice(idx, 1)
      }
    }
  }

}