import React from 'react'
import { useSelector } from 'react-redux'
import ReactTooltip from 'react-tooltip'
import MenuComponents from './MenuComponents'
import { BackButton, CornerBackButton, CustomMenuButton, Inline } from './ShopMenu'

export default ({ backBtnFn }) => {

  const { gameObjects } = window
  const state = useSelector(state => state)
  let achievementsKeys = Object.keys(state.game.achievements)

  return (
    <>
      <MenuComponents.Container
      width={ gameObjects ? gameObjects.getKeyValue('game-width') * .75 : 1200 * .75 }
      height={ gameObjects ? gameObjects.getKeyValue('game-height') * .75 : 550 * .75 }
      >
        <MenuComponents.Backing
        width={ gameObjects ? gameObjects.getKeyValue('game-width') * .75 : 1200 * .75 }
        height={ gameObjects ? gameObjects.getKeyValue('game-height') * .75 : 550 * .75 }
        ></MenuComponents.Backing>
        <MenuComponents.MenuTitle>Achievements</MenuComponents.MenuTitle>
        <MenuComponents.MenuButtonContainer>
            {
              achievementsKeys.map(key => {
                if (state.game.achievements[key].owned) {
                  return (
                    <React.Fragment key={key}>
                    <Inline>
                      <MenuComponents.MenuButtonSpacer data-tip data-for={key+'-description'}>
                        <CustomMenuButton>
                          {key}
                        </CustomMenuButton>
                      </MenuComponents.MenuButtonSpacer>
                    </Inline>
                    <ReactTooltip id={key+'-description'}>
                      <span>
                        Earned by: {state.game.achievements[key].description}
                      </span>
                    </ReactTooltip>
                    </React.Fragment>
                  )
                }
              })
            }
          <CornerBackButton marginTop={'-60px'} onClick={ backBtnFn } />
        </MenuComponents.MenuButtonContainer>
      </MenuComponents.Container>
    </>
  )

}