import React from 'react'
import styled from 'styled-components'
import MenuComponents from './MenuComponents'
import { Inline } from './ShopMenu'

const { MenuButton, MenuButtonContainer, MenuButtonSpacer } = MenuComponents

const BottomBanner = styled.div`
height: 120px;
width: 100%;
position: absolute;
bottom: 0;
`

const Back = styled.div`
height: 120px;
width: 100%;
background-color: #333;
opacity: .8;
`

export default () => {

  return (
    <>
      <BottomBanner className='shop-query-menu'>
        <Back />
        <MenuButtonContainer space-top={2}>
          <MenuButtonSpacer>
            <Inline>
              <MenuButtonSpacer padding={4}>
                <MenuButton bgcolor={'#555'} width={500} height={40}>Done Placing Barriers/Traps: Press [ B ]</MenuButton>
              </MenuButtonSpacer>
            </Inline>
          </MenuButtonSpacer>
        </MenuButtonContainer>
      </BottomBanner>
    </>
  )

}