import * as PIXI from 'pixi.js'

const { TweenLite: { to, from, kill } } = window

export default function Crosshairs(gameObjects) {
	let container = gameObjects.getKeyValue('game-pixi-container')
	this.crosshair = new PIXI.Container()
	this.cross = new PIXI.Graphics()
	this.cross.lineStyle(2, 0xB00000, 0.75)
	this.cross.moveTo(0, -15)
	this.cross.lineTo(0, 15)
	this.cross.moveTo(-15, 0)
	this.cross.lineTo(15, 0)
	this.crosshair.addChild(this.cross)
	this.circle = new PIXI.Graphics()
	this.circle.lineStyle(1, 0xB00000, 0.75)
	this.circle.drawCircle(0, 0, 10)
	this.crosshair.addChild(this.circle)
  this.changed = false
  this.changeBack = false
	this._new = null
  container.addChild(this.crosshair)

	this.crosshair.buildArrow = (color) => {
		let crosshair = gameObjects.getKeyValue('crosshair')
    container.removeChild(crosshair)
    let _new = new PIXI.Graphics()
    _new.lineStyle(2, color, 1.0)
    _new.beginFill(0x777777, 0.0)
    _new.moveTo(0,0)
    _new.lineTo(20, 0)
    _new.lineTo(20, 30)
    _new.lineTo(30, 30)
    _new.lineTo(10, 45)
    _new.lineTo(-10, 30)
    _new.lineTo(0, 30)
    _new.lineTo(0, 0)
    _new.endFill()
    _new.pivot.x = _new.width * .5
    _new.pivot.y = _new.height * .5
    container.addChild(_new)
    this._new = _new
    crosshair._new = _new
    crosshair.changed = true
    gameObjects.setKeyValue('object-placement-arrow', _new)
    // animated arrow loop
    this.animationLoop = () => {
      to(_new.scale, .5, {
        x: 1.2,
        y: .75,
        onComplete: () => {
          to(_new.scale, .5, {
            x: .9,
            y: 1.2,
            onComplete: this.animationLoop
          })
        }
      })
    }
    this.animationLoop()

		return _new
	}

	return this
}
