import { contains } from 'jquery'
import { AnimatedSprite } from 'pixi.js'
import { AdjustmentFilter } from '@pixi/filter-adjustment';
import { BlurFilter } from '@pixi/filter-blur'
import { GlowFilter } from '@pixi/filter-glow';
import store from '../../../app/store'

export default class ExplosionSprite {

  constructor(type, renderBody) {
    this.gameObjects = window.gameObjects
    let { to } = window.TweenLite
    this.container = this.gameObjects.getKeyValue('game-pixi-container')
    let explosionTexture1 = this.gameObjects.getKeyValue('explosion3d1-sheet')
    let explosionTexture2 = this.gameObjects.getKeyValue('explosion3d2-sheet')
    this.explosion1 = new AnimatedSprite( explosionTexture1.animations['explosion3d'] )
    this.explosion2 = new AnimatedSprite( explosionTexture2.animations['explosion3d'] )
    this.explosions = [ this.explosion1, this.explosion2 ]
    this.explosions.forEach(ex => {
      ex.pivot.x = ex.width * .5
      ex.pivot.y = ex.height * .5
      ex.loop = false
      ex.onComplete = () => {
        this.container.removeChild(ex)
        this.container = null
        this.gameObjects = null
        explosionTexture1 = null
        explosionTexture2 = null
        this.explosions = null
      }
    })
    if (type === 1) {
      this.explosion = this.explosion1
      this.explosion.animationSpeed = 1.0
    }
    if (type === 2) {
      this.explosion = this.explosion2
      this.explosion.animationSpeed = 1.0
    }
    // this.container.addChild(this.explosion)
    this.renderBody = renderBody
    let adjustment = new AdjustmentFilter()
    adjustment.brightness = 20.0
    adjustment.saturation = 3.0
    let blur = new BlurFilter()
    blur.blur = .5
    let glow = new GlowFilter({
      outerStrength: 3.0
    })
    this.filters = store.getState().game.filterEffects ? [
      adjustment,
      blur,
      glow
    ] : []
    this.explosion.filters = this.filters
    to(this.explosion, .35, { alpha: 0 })
    to(adjustment, .5, { brightness: 1.0, saturation: 1.5, onUpdate: () => {
      if (store.getState().game.filterEffects) {
        this.explosion.filters = this.filters
      }
    }})
    this.explosion.play()
  }

  // checkFilters() {
  //   if (store.getState().game.filterEffects) {
  //     this.explosion.filters = this.filters
  //   } else {
  //     this.explosion.filters = []
  //   }
  // }

  renderPosition(renderFn) {
    if (this.renderBody) {
      this.explosion.position = renderFn(this.renderBody)
      if (this.container && this.container.children.indexOf(this.explosion) === -1) {
        this.container.addChild(this.explosion)
      }
    }
  }

}