import { Bodies, Constraint, World } from 'matter-js'
import * as PIXI from 'pixi.js'
import { getBodyHeight, getBodyWidth, makePixiSprite } from '../utils/Utils'
import GrenadeExplosion from '../FX/GrenadeExplosion'
import store from '../../../app/store'

export default function WallBarrier (pos, dimensions, type) {
  let { getState } = store
  let { gameObjects, TweenLite:{ delayedCall } } = window
  let barriersSprites = gameObjects.getKeyValue('barriers-sheet')
  let container = gameObjects.getKeyValue('game-pixi-container')
  let world = gameObjects.getKeyValue('world')
  let buildObjects = gameObjects.getKeyValue('build-objects')
  let {width:w,height:h} = dimensions
  let woodenBarrier = new PIXI.Sprite(barriersSprites.textures['woodenBarrier.png'])
  let stoneBarrier = new PIXI.Sprite(barriersSprites.textures['stoneBarrier.png'])
  let metalBarrier = new PIXI.Sprite(barriersSprites.textures['metalBarrier.png'])
  let explosiveBarrier = makePixiSprite('explosive-barrel')
  this.types = {
    WOOD: 'Wooden',
    STONE: 'Stone',
    METAL: 'Metal',
    EXPLOSIVE: 'Explosive Barrel'
  }
  let {x,y} = pos
  let objBody = Bodies.rectangle(x, y, w, h, {
    inertia: Infinity,
    label: type === this.types.EXPLOSIVE ? 'explosive barrier' : 'barrier'
  })
  // try to attach barrier to the ground
  let ground = gameObjects.getKeyValue('ground')
  let constraint = Constraint.create({
    bodyA: ground,
    bodyB: objBody,
    pointA: { x: -getBodyWidth(ground)/2 + pos.x, y: -getBodyHeight(ground)/2 },
    pointB: { x: 0, y: getBodyHeight(objBody)/2 },
    length: 0
  })
  if (type) {
    switch (type) {
      case this.types.WOOD:
        objBody.strength = getState().game.player.perks['barrierPlus'].owned ? 300 : 100
        break;
      case this.types.STONE:
        objBody.strength = getState().game.player.perks['barrierPlus'].owned ? 500 : 250
        break;
      case this.types.METAL:
        objBody.strength = getState().game.player.perks['barrierPlus'].owned ? 800 : 400
        break;
      case this.types.EXPLOSIVE:
        objBody.strength = getState().game.player.perks['barrierPlus'].owned ? 200 : 100
        break;
      default:
        objBody.strength = getState().game.player.perks['barrierPlus'].owned ? 300 : 100
    }
  }

  objBody.main = this
  this.destroyed = false

  this.getBody = () => objBody

  // World.add(world, objBody)
  World.add(world, [constraint, objBody])

  let barrierSprite
  let txtStyle = { fill: '#ffffff', fontSize: 12 }
  let healthDisplay = new PIXI.Text(objBody.strength, txtStyle)
  healthDisplay.pivot.x = healthDisplay.width * .5
  healthDisplay.pivot.y = healthDisplay.height * .5
  if (type) {
    switch (type) {
      case this.types.WOOD:
        barrierSprite = woodenBarrier
        break;
      case this.types.STONE:
        barrierSprite = stoneBarrier
        break;
      case this.types.METAL:
        barrierSprite = metalBarrier
        break;
      case this.types.EXPLOSIVE:
        barrierSprite = explosiveBarrier
        break;
      default:
    }
  }
  barrierSprite.width = w
  barrierSprite.height = h
  let sprite = new PIXI.Container()
  sprite.addChild(barrierSprite)
  sprite.pivot.x = sprite.width * .5
  sprite.pivot.y = sprite.height * .5
  container.addChild(sprite)
  container.addChild(healthDisplay)

  let player = gameObjects.getKeyValue('player')
  player.sprites.forEach(sprite => { container.addChild(sprite) })

  this.takeDamage = (dmg) => {
    objBody.strength -= dmg
    healthDisplay.text = objBody.strength
  }

  this.displayBarrierStrength = (renderFn) => {
    healthDisplay.position = renderFn(objBody)
    barrierSprite.position = renderFn(objBody)
  }

  objBody.removeSelf = () => {
    this.destroyed = true
    new GrenadeExplosion(objBody.position, true, {
      showWood: type === this.types.WOOD ? true : false,
      showExplosionSprite: type === this.types.EXPLOSIVE ? true : false
    })
    if (type === this.types.EXPLOSIVE) {
      let {x:_x,y:_y} = objBody.position
      let explosionHitbox = Bodies.rectangle(_x, _y, 400, 300)
      explosionHitbox.label = 'barrel explosion'
      World.add(world, explosionHitbox)
      delayedCall(2, () => {
        if (explosionHitbox) {
          World.remove(world, explosionHitbox)
        }
      })
      explosionHitbox.removeSelf = () => {
        World.remove(world, explosionHitbox)
      }
    }
    World.remove(world, [constraint, objBody])
    container.removeChild(healthDisplay)
    container.removeChild(sprite)
    let idx = buildObjects.indexOf(this)
    if (idx > -1) { buildObjects.splice(idx, 1) }
  }

  return this
}